<template>
  <div
      class=" flex flex-col justify-center items-center rounded-lg h-screen m-auto"
  >
    <img src="@//assets//images//unap.png" alt="" class=" w-10"/>

    <div class=" mt-5">
      <p class=" text-center text-2xl px-3 font-semibold ">Iniciar Sesi贸n DA</p>
      <p class=" text-xs text-gray-400">Direcci贸n de Admisi贸n</p>
    </div>

    <form
        @submit.prevent=""
        class="p-10 shadow-md rounded-3xl center content-inputs"
    >

      <div class="bg-gray-100 p-2 rounded-xl">
        <span class="text-sm text-gray-500">Esquema:</span>
        <select class="border rounded-3xl p-2 flex flex-col focus:outline-none" v-model="scheme" @change="changeSchemaEvent">
          <option v-for="(item,i) in domainsData" :key="i" :value="item" >
            {{ item.name }}
          </option>
        </select>
      </div>

      <div class="mt-5">
        <label class=" text-sm text-gray-500">Usuario</label>
        <vs-input v-model.trim="username" autofocus id="txtusername">
          <template #icon>
            <i class="fas fa-user text-green-400"></i>
          </template>
        </vs-input>
      </div>

      <div class=" mt-2">
        <label class=" text-sm text-gray-500">Contrase帽a</label>
        <vs-input type="password" icon-after v-model.trim="password">
          <template #icon>
            <i class="fas fa-key text-green-400"></i>
          </template>
        </vs-input>
      </div>

      <div class="mt-4">
        <vs-button @click="login" class=" focus:outline-none w-full" success>
          Ingresar
        </vs-button>
      </div>
    </form>
  </div>
</template>

<script>
import axios from "@//plugins//axios.js";

export default {
  data() {
    return {
      username: "",
      password: "",
      domainsData: [],
      scheme: null
    };
  },

  mounted() {
    localStorage.removeItem("other-domain")
    this.getClients();
    setTimeout(() => {
      document.getElementById("vs-input--txtusername").focus();
    }, 200);
  },

  methods: {
    changeSchemaEvent(){
      console.log("")
    },

    async getClients() {
      const {data} = await axios.get("tenant/GetClients")
      this.domainsData = data
    },

    async login() {
      if (this.username.length <= 0 || this.password.length <= 0) {
        this.$vs.notification({
          title: "Inicio de Sesi贸n",
          text: "Ingrese su usuario y contrase帽a!  ",
          color: "danger",
        });
        return;
      }

      //
      const loading = this.$vs.loading({
        color: "success",
      });

      localStorage.setItem("other-domain", JSON.stringify(this.scheme))

      //
      await this.$store
          .dispatch("auth/set_auth", {
            username: this.username,
            password: this.password,
          })
          .then((res) => {
            if (!res) {
              this.$vs.notification({
                title: "Inicio de Sesi贸n",
                text: "No se pudo validar el usuario y contrase帽a! ",
                color: "danger",
              });

              return;
            }

            this.$vs.notification({
              title: "Inicio de Sesi贸n",
              text: "Usuario validado correctamente! ",
              color: "success",
            });

            this.$router.push("/");
          });

      loading.close();
    },
  },
};
</script>
